
import { defineComponent } from "vue";
import Dropdown, { DropdownItem } from "./Dropdown.vue";
import { MutationTypes } from "@/store/mutations";
import { User } from "@/store/state";
import { logout } from "@/scripts/scrapers";

export default defineComponent({
  name: "User",
  components: {
    Dropdown,
  },
  data() {
    return {
      showDropdown: false,
    };
  },
  mounted() {
    this.$emitter.on("show-user-card", this.showCard);
    const userEl = this.$refs.user as HTMLElement;
    new ResizeObserver(() => {
      this.$reactive.userOffsetWidth = userEl.offsetWidth + "px";
    }).observe(userEl);
  },
  methods: {
    async dropdownClosed(rowName: string | undefined) {
      this.showDropdown = false;
      if (rowName == "Odjava" && this.user && (await logout())) {
        this.$store.commit(MutationTypes.UPDATE_USER_STATUS, {
          user: this.user,
          status: false,
        });
      }
    },
    showCard(show: boolean) {
      const userEl = this.$refs.user as HTMLElement;
      if (userEl) userEl.classList[show ? "add" : "remove"]("card");
    },
  },
  computed: {
    user(): User | undefined {
      return this.$store.getters.user;
    },
    dropdown(): DropdownItem[] {
      return [
        {
          name: "Osobni podaci",
          icon: "fingerprint",
          link:
            "/razred/" + (this.$route.params.classId || "-") + "/osobni-podaci",
        },
        {
          name: "Odjava",
          icon: "exit_to_app",
        },
      ];
    },
  },
  watch: {
    $route(to) {
      this.showCard(to.path.includes("/stranica/"));
    },
  },
});
